import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { Box, Button, MenuItem, OutlinedInput, Select } from '@mui/material'
import {
  useGetProfessionQuery,
  useDeleteProfessionMutation,
} from '@Services/admin'
import { useDispatch } from 'react-redux'
import { errorAlert } from '../../../Redux/Reducers/genericSlice'

const DeleteForm = ({ closeDrawer }) => {
  const dispatch = useDispatch()
  const [selectedProfession, setProfession] = useState('')
  const { data } = useGetProfessionQuery()
  const [deleteProfession, { isLoading }] = useDeleteProfessionMutation()

  return (
    <Box>
      <Box>
        <Select
          fullWidth
          displayEmpty
          value={selectedProfession}
          onChange={(event) => {
            setProfession(event.target.value)
          }}
          input={<OutlinedInput />}
          inputProps={{ 'aria-label': 'Without label' }}
        >
          <MenuItem disabled value="">
            <em>Select Profession</em>
          </MenuItem>
          {data?.professions.map((profession) => (
            <MenuItem key={profession._id} value={profession._id}>
              {profession.professionName}
            </MenuItem>
          ))}
        </Select>
      </Box>
      <Box width="100%" position="absolute" bottom={0} sx={{ mt: 2 }}>
        <Button
          disabled={isLoading}
          fullWidth
          variant="contained"
          color="secondary"
          onClick={async () => {
            if (selectedProfession) {
              const response = await deleteProfession(selectedProfession)
              if (!response.error) {
                closeDrawer()
              }
            } else {
              dispatch(errorAlert({ message: 'Select the profession.' }))
            }
          }}
        >
          Submit
        </Button>
      </Box>
    </Box>
  )
}

export default DeleteForm

DeleteForm.defaultProps = {
  closeDrawer: () => null,
}

DeleteForm.propTypes = {
  closeDrawer: PropTypes.func,
}
