import React from 'react'
import { Box, Typography } from '@mui/material'

const Header = () => {
  return (
    <Box
      display="flex"
      justifyContent="space-between"
      alignItems="center"
      p={2}
    >
      <Box>Logo</Box>
      <Box
        display="flex"
        width={160}
        justifyContent="space-between"
        alignItems="center"
      >
        <Typography>How it works?</Typography>
        <Typography>FAQ</Typography>
      </Box>
    </Box>
  )
}

export default Header
