import React, { useState } from 'react'
import PropTypes from 'prop-types'
import {
  Box,
  Button,
  FormControl,
  InputLabel,
  OutlinedInput,
} from '@mui/material'
import { useDispatch } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { useGetTokenMutation } from '@Services/admin'
import md5 from 'md5'
import { errorAlert } from '../../../Redux/Reducers/genericSlice'

const AdminForm = ({ closeDrawer }) => {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [getToken, { isLoading }] = useGetTokenMutation()

  return (
    <Box>
      <Box>
        <InputLabel sx={{ mb: 1 }}>Username</InputLabel>
        <FormControl fullWidth sx={{ mb: 2 }}>
          <OutlinedInput
            hiddenLabel
            placeholder="Enter the username"
            value={username}
            onChange={(event) => {
              setUsername(event.target.value)
            }}
          />
        </FormControl>
        <InputLabel sx={{ mb: 1 }}>Password</InputLabel>
        <FormControl fullWidth sx={{ mb: 2 }}>
          <OutlinedInput
            type="password"
            hiddenLabel
            placeholder="Enter the password"
            value={password}
            onChange={(event) => {
              setPassword(event.target.value)
            }}
          />
        </FormControl>
      </Box>
      <Box width="100%" position="absolute" bottom={0} sx={{ mt: 2 }}>
        <Button
          disabled={isLoading}
          fullWidth
          variant="contained"
          color="secondary"
          onClick={async () => {
            if (username.trim() && password.trim()) {
              const response = await getToken({
                username,
                password: md5(password),
              })
              if (!response.error) {
                sessionStorage.setItem('token', response.data.token)
                closeDrawer()
                navigate('/')
              }
            } else {
              dispatch(errorAlert({ message: 'Fill all the details.' }))
            }
          }}
        >
          Submit
        </Button>
      </Box>
    </Box>
  )
}

export default AdminForm

AdminForm.defaultProps = {
  closeDrawer: () => null,
}

AdminForm.propTypes = {
  closeDrawer: PropTypes.func,
}
