import { createTheme } from '@mui/material/styles'

const COLORS = {
  mode: 'dark',
  secondary: { main: '#9747FF' },
  green: { main: '#00DA30' },
  white: {
    main: '#fff',
    light: '#1B1B1B',
  },
  black: {
    main: '#000',
    main100: '#121212',
  },
}

const Default = createTheme({
  palette: COLORS,
  components: {
    MuiButton: {
      styleOverrides: {
        root: { borderRadius: '16px' },
      },
      defaultProps: { disableRipple: true },
    },
  },
})

export default Default
