import React from 'react'
import PropTypes from 'prop-types'
import { Box } from '@mui/material'
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from 'chart.js'
import { Radar } from 'react-chartjs-2'

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
)

const Chart = ({ data }) => {
  const chartData = {
    labels: Object.keys(data),
    datasets: [
      {
        label: '',
        data: Object.values(data),
        backgroundColor: 'rgba(255, 99, 132, 0.2)',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth: 1,
      },
    ],
  }

  return (
    <Box sx={{ marginLeft: 'auto', marginRight: 'auto' }} mt={3} mb={3}>
      <Radar
        width={400}
        height={400}
        data={chartData}
        options={{
          plugins: {
            legend: {
              display: false,
            },
          },
          maintainAspectRatio: false,
          scales: {
            r: {
              min: 0,
              max: 5,
              angleLines: { color: 'gray' },
              grid: { color: 'gray' },
              pointLabels: {
                color: 'white',
              },
              ticks: {
                display: false,
                color: 'white',
                backdropColor: 'transparent',
                stepSize: 1,
              },
            },
          },
        }}
      />
    </Box>
  )
}

export default Chart

Chart.propTypes = {
  data: PropTypes.oneOfType([PropTypes.object]).isRequired,
}
