import React, { useState } from 'react'
import PropTypes from 'prop-types'
import {
  Box,
  Button,
  MenuItem,
  OutlinedInput,
  Select,
  Typography,
  styled,
} from '@mui/material'
import CloudUploadIcon from '@mui/icons-material/CloudUpload'
import { useDispatch } from 'react-redux'
import {
  useGetProfessionQuery,
  useUploadQuestionsMutation,
} from '@Services/admin'
import { errorAlert } from '../../../Redux/Reducers/genericSlice'

const VisuallyHiddenInput = styled('input')({
  clip: 'rect(0 0 0 0)',
  clipPath: 'inset(50%)',
  height: 1,
  overflow: 'hidden',
  position: 'absolute',
  bottom: 0,
  left: 0,
  whiteSpace: 'nowrap',
  width: 1,
})

const UploadForm = ({ closeDrawer }) => {
  const dispatch = useDispatch()
  const [selectedProfession, setProfession] = useState('')
  const [selectedFile, setSelectedFile] = useState(null)
  const { data } = useGetProfessionQuery()
  const [uploadQuestions, { isLoading: isSubmitting }] =
    useUploadQuestionsMutation()

  const uploadFile = async () => {
    if (selectedFile) {
      const formData = new FormData()
      const fileToUpload = new File([selectedFile], selectedFile.name)
      formData.append('professionId', selectedProfession)
      formData.append('file', fileToUpload)
      const response = await uploadQuestions(formData)
      if (!response.error) {
        closeDrawer()
      }
    } else {
      dispatch(errorAlert({ message: 'Fill all the details.' }))
    }
  }
  return (
    <Box>
      <Box>
        <Box mb={2}>
          <Select
            fullWidth
            displayEmpty
            value={selectedProfession}
            onChange={(event) => {
              setProfession(event.target.value)
            }}
            input={<OutlinedInput />}
            inputProps={{ 'aria-label': 'Without label' }}
          >
            <MenuItem disabled value="">
              <em>Select Profession</em>
            </MenuItem>
            {data?.professions.map((profession) => (
              <MenuItem key={profession._id} value={profession._id}>
                {profession.professionName}
              </MenuItem>
            ))}
          </Select>
        </Box>
        <Button
          fullWidth
          color="warning"
          component="label"
          variant="contained"
          startIcon={<CloudUploadIcon />}
        >
          Upload file
          <VisuallyHiddenInput
            type="file"
            accept=".csv"
            onChange={(e) => setSelectedFile(e.target.files[0])}
          />
        </Button>
        <Typography align="center" mt={1}>
          {selectedFile?.name}
        </Typography>
      </Box>
      <Box width="100%" position="absolute" bottom={0} sx={{ mt: 2 }}>
        <Button
          disabled={isSubmitting}
          fullWidth
          variant="contained"
          color="secondary"
          onClick={uploadFile}
        >
          Submit
        </Button>
      </Box>
    </Box>
  )
}

export default UploadForm

UploadForm.defaultProps = {
  closeDrawer: () => null,
}

UploadForm.propTypes = {
  closeDrawer: PropTypes.func,
}
