import React, { useEffect } from 'react'
import {
  Box,
  Skeleton,
  Grid,
  Typography,
  useMediaQuery,
  useTheme,
} from '@mui/material'
import { useNavigate, useSearchParams } from 'react-router-dom'
import { useGetAnswerQuery } from '@Services/admin'
import Chart from './Chart/Chart'
import Styled from './style'

const Result = () => {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.down('md'))
  const [searchParams] = useSearchParams()
  const resultId = searchParams.get('id')
  const navigate = useNavigate()
  const { data, isLoading } = useGetAnswerQuery(
    { answerId: resultId },
    {
      skip: !resultId,
    },
  )

  const isEmpty = (obj) => {
    return Object.keys(obj).length === 0
  }

  useEffect(() => {
    if (!isLoading && (!data?.data || isEmpty(data?.data))) {
      navigate('/')
    }
  }, [isLoading])

  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  return (
    <Box maxWidth={550} sx={{ marginLeft: 'auto', marginRight: 'auto' }}>
      <Box display="flex" alignItems="center" justifyContent="space-between">
        <Box>
          <Typography variant="h5" mb={1} fontWeight={600}>
            Meet your result
          </Typography>
          <Typography variant="body1">
            {data?.profession?.professionName}
          </Typography>
        </Box>
      </Box>
      {isLoading ? (
        <Box mt={4}>
          <Styled.ResultWrapper>
            <Typography variant="body1" fontWeight={600}>
              <Skeleton
                animation="wave"
                variant="rectangular"
                width={160}
                height={20}
                sx={{ mb: 1 }}
              />
            </Typography>
            <Box>
              <Skeleton
                animation="wave"
                variant="rectangular"
                width={matches ? 320 : 510}
                height={300}
                sx={{ mb: 1 }}
              />
            </Box>
          </Styled.ResultWrapper>
        </Box>
      ) : (
        Object.keys(data?.data || []).map((keyName) => (
          <Box mt={4}>
            <Styled.ResultWrapper>
              <Typography variant="body1" fontWeight={600}>
                {keyName}
              </Typography>
              <Chart data={data?.data[keyName]} />
              {data?.improvements[keyName] && (
                <Box>
                  <Grid container columnSpacing={3} rowSpacing={2}>
                    <Grid item xs={6}>
                      <Typography variant="body1" fontWeight={600}>
                        Strengths
                      </Typography>
                      <Box component="ul" sx={{ padding: 0, mt: 2, mb: 0 }}>
                        {data?.strengths[keyName].map((val, indx) => (
                          <Box
                            // eslint-disable-next-line react/no-array-index-key
                            key={indx}
                            // component="li"
                            sx={{ listStylePosition: 'inside', mb: 1 }}
                          >
                            {val}
                          </Box>
                        ))}
                      </Box>
                    </Grid>
                    <Grid item xs={6}>
                      <Typography variant="body1" fontWeight={600}>
                        Worth to improve
                      </Typography>
                      <Box component="ul" sx={{ padding: 0, mt: 2, mb: 0 }}>
                        {data?.improvements[keyName].map((val, indx) => (
                          <Box
                            // eslint-disable-next-line react/no-array-index-key
                            key={indx}
                            // component="li"
                            sx={{ listStylePosition: 'inside', mb: 1 }}
                          >
                            {val}
                          </Box>
                        ))}
                      </Box>
                    </Grid>
                  </Grid>
                </Box>
              )}
            </Styled.ResultWrapper>
          </Box>
        ))
      )}
    </Box>
  )
}

export default Result
