import React, { useState } from 'react'
import PropTypes from 'prop-types'
import {
  Box,
  Button,
  FormControl,
  InputLabel,
  OutlinedInput,
} from '@mui/material'
import { useDispatch } from 'react-redux'
import { useAddProfessionMutation } from '@Services/admin'
import { errorAlert } from '../../../Redux/Reducers/genericSlice'

const AddForm = ({ closeDrawer }) => {
  const dispatch = useDispatch()
  const [title, setTitle] = useState('')
  const [description, setDescription] = useState('')
  const [addProfession, { isLoading }] = useAddProfessionMutation()

  return (
    <Box>
      <Box>
        <InputLabel sx={{ mb: 1 }}>Title</InputLabel>
        <FormControl fullWidth sx={{ mb: 2 }}>
          <OutlinedInput
            hiddenLabel
            placeholder="Enter the title"
            value={title}
            onChange={(event) => {
              setTitle(event.target.value)
            }}
          />
        </FormControl>
      </Box>
      <Box>
        <InputLabel sx={{ mb: 1 }}>Description</InputLabel>
        <FormControl fullWidth sx={{ mb: 2 }}>
          <OutlinedInput
            hiddenLabel
            placeholder="Enter the description"
            value={description}
            onChange={(event) => {
              setDescription(event.target.value)
            }}
          />
        </FormControl>
      </Box>
      <Box width="100%" position="absolute" bottom={0} sx={{ mt: 2 }}>
        <Button
          disabled={isLoading}
          fullWidth
          variant="contained"
          color="secondary"
          onClick={async () => {
            if (title.trim() && description.trim()) {
              const response = await addProfession({
                professionName: title,
                description,
              })
              if (!response.error) {
                closeDrawer()
              }
            } else {
              dispatch(errorAlert({ message: 'Fill all the details.' }))
            }
          }}
        >
          Submit
        </Button>
      </Box>
    </Box>
  )
}

export default AddForm

AddForm.defaultProps = {
  closeDrawer: () => null,
}

AddForm.propTypes = {
  closeDrawer: PropTypes.func,
}
