import React, { useEffect } from 'react'
import { Alert, Box } from '@mui/material'
import { useSelector, useDispatch } from 'react-redux'
import { removeAlert } from '../../Redux/Reducers/genericSlice'
import usePrevious from '../../Hooks/usePrevious'

const AlertBox = () => {
  const { alerts } = useSelector((state) => state.alert)
  const dispatch = useDispatch()
  const prevAlerts = usePrevious(alerts)

  const handleOnClose = () => {
    dispatch(removeAlert())
  }

  useEffect(() => {
    // dispatch removeAlert only after new alerts are added.
    if (alerts && prevAlerts && alerts.length > prevAlerts.length) {
      setTimeout(() => {
        dispatch(removeAlert())
      }, 3000)
    }
  }, [alerts, dispatch, prevAlerts])

  return (
    <>
      {!!alerts?.length && (
        <Box
          sx={{
            position: 'fixed',
            zIndex: '1201',
            right: '10px',
            top: '10px',
          }}
        >
          {alerts.map((eachAlert) => {
            return (
              <Alert
                key={eachAlert.key}
                onClose={handleOnClose}
                severity={eachAlert.severity}
                sx={{ marginBottom: '6px' }}
              >
                {/* <AlertTitle>{eachAlert.title}</AlertTitle> */}
                {eachAlert.message}
              </Alert>
            )
          })}
        </Box>
      )}
    </>
  )
}

export default AlertBox
