import React, { Suspense } from 'react'
import {
  BrowserRouter,
  Outlet,
  Routes as ROUTES,
  Route,
} from 'react-router-dom'
import Layout from '@Components/Layout/Layout'
import Profession from '@Container/Profession/Profession'
import Questions from '@Container/Questions/Questions'
import Results from '@Container/Result/Result'

const Routes = () => {
  return (
    <Suspense fallback={<div>spinner</div>}>
      <BrowserRouter>
        <ROUTES>
          <Route
            exact
            path="/"
            element={
              <Layout>
                <Outlet />
              </Layout>
            }
          >
            <Route path="/" element={<Profession />} />
            <Route path="/questions" element={<Questions />} />
            <Route path="/result" element={<Results />} />
            <Route path="/admin" element={<Profession />} />
          </Route>
          <Route path="*" element={<Profession />} />
        </ROUTES>
      </BrowserRouter>
    </Suspense>
  )
}

export default Routes
