import { signOut } from '../Redux/Reducers/UserSlice'
import { errorAlert } from '../Redux/Reducers/genericSlice'

// eslint-disable-next-line import/prefer-default-export
export const onMutationStarted = async (_, { dispatch, queryFulfilled }) => {
  try {
    await queryFulfilled
  } catch (err) {
    if (err.error && err.error.status && err.error.status === 401) {
      // if unauthorization error,logout user.
      dispatch(signOut())
      dispatch(
        errorAlert({
          message: 'Session expired, please sign in again !',
        }),
      )
    } else {
      dispatch(
        errorAlert({
          message: err?.error?.data?.message || 'Error in completing request.',
        }),
      )
    }
  }
}
