import React from 'react'
import PropTypes from 'prop-types'
import { Box, Button, Typography } from '@mui/material'
import { useDeleteQuestionMutation } from '@Services/admin'

const DeleteForm = ({ data, closeDrawer }) => {
  const [deleteQuestion, { isLoading }] = useDeleteQuestionMutation(data?._id)

  return (
    <Box>
      <Typography variant="h6" align="center">
        Are you sure ?
      </Typography>
      <Typography
        variant="body2"
        align="center"
        fontStyle="italic"
        color="error"
      >
        {data?.question}
      </Typography>
      <Box width="100%" position="absolute" bottom={0} sx={{ mt: 2 }}>
        <Button
          disabled={isLoading}
          fullWidth
          variant="contained"
          color="secondary"
          onClick={async () => {
            const response = await deleteQuestion(data?._id)
            if (!response.error) {
              closeDrawer()
            }
          }}
        >
          Submit
        </Button>
      </Box>
    </Box>
  )
}

export default DeleteForm

DeleteForm.defaultProps = {
  closeDrawer: () => null,
  data: null,
}

DeleteForm.propTypes = {
  closeDrawer: PropTypes.func,
  data: PropTypes.oneOfType([PropTypes.object]),
}
