import React, { forwardRef, useImperativeHandle, useState } from 'react'
import PropTypes from 'prop-types'
import Box from '@mui/material/Box'
import Drawer from '@mui/material/Drawer'
import { Typography, useMediaQuery, useTheme } from '@mui/material'
import CloseIcon from '@mui/icons-material/Close'

const MuiDrawer = forwardRef(
  ({ element, children, title, isClosable }, ref) => {
    const theme = useTheme()
    const matches = useMediaQuery(theme.breakpoints.down('md'))
    const [state, setState] = useState({
      right: false,
    })

    const openDrawer = () => {
      setState({ ...state, right: true })
    }

    const closeDrawer = () => {
      setState({ ...state, right: false })
    }

    useImperativeHandle(
      ref,
      () => {
        return {
          openDrawer,
          closeDrawer,
        }
      },
      [],
    )

    const toggleDrawer = (anchor, open) => (event) => {
      if (
        (event.type === 'keydown' && event.key === 'Tab') ||
        event.key === 'Shift'
      ) {
        return
      }

      setState({ ...state, [anchor]: open })
    }

    const list = (anchor) => (
      <Box
        sx={{
          width: matches ? 250 : 400,
          padding: '8px',
          backgroundColor: theme.palette.black.main,
        }}
        role="presentation"
      >
        <Box height="calc(100vh - 16px)" position="relative">
          <Box position="relative">
            <Typography mb={2} variant="h6" fontWeight={600} align="center">
              {title}
            </Typography>
            {isClosable && (
              <Box
                sx={{
                  position: 'absolute',
                  right: 0,
                  top: '58%',
                  transform: 'translate(0, -50%)',
                  svg: {
                    cursor: 'pointer',
                    fontSize: '28px',
                    padding: '5px',
                    border: `2px solid ${theme.palette.white.light}`,
                    borderRadius: '30px',
                  },
                }}
                component="span"
                onClick={toggleDrawer(anchor, false)}
              >
                <CloseIcon />
              </Box>
            )}
          </Box>
          {children}
        </Box>
      </Box>
    )

    return (
      <>
        {['right'].map((anchor) => (
          <React.Fragment key={anchor}>
            <Box component="span" onClick={toggleDrawer(anchor, true)}>
              {element}
            </Box>
            <Drawer anchor={anchor} open={state[anchor]}>
              {list(anchor)}
            </Drawer>
          </React.Fragment>
        ))}
      </>
    )
  },
)

export default MuiDrawer

MuiDrawer.defaultProps = {
  isClosable: true,
}

MuiDrawer.propTypes = {
  title: PropTypes.string.isRequired,
  element: PropTypes.node.isRequired,
  children: PropTypes.node.isRequired,
  isClosable: PropTypes.bool,
}
