import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'
import { onMutationStarted } from './serviceUtility'

// Define a service using a base URL and expected endpoints
export const adminApi = createApi({
  reducerPath: 'adminApi',
  baseQuery: fetchBaseQuery({
    baseUrl: `${process.env.REACT_APP_Base_Url}`,
    prepareHeaders: (headers) => {
      const token = sessionStorage.getItem('token')
      if (token) {
        headers.set('token', token)
      }
      return headers
    },
  }),
  tagTypes: ['Professions', 'Questions'],
  endpoints: (builder) => ({
    getToken: builder.mutation({
      query: (data) => {
        return {
          url: `/user/token`,
          method: 'POST',
          body: { ...data },
        }
      },
    }),
    // Profession Api's
    getProfession: builder.query({
      query: () => {
        return {
          url: `/professions/all`,
          method: 'GET',
        }
      },
      providesTags: ['Professions'],
      keepUnusedDataFor: 0,
    }),
    addProfession: builder.mutation({
      query: (data) => {
        return {
          url: `/professions/add`,
          method: 'POST',
          body: { ...data },
        }
      },
      onQueryStarted: onMutationStarted,
      invalidatesTags: ['Professions'],
    }),
    deleteProfession: builder.mutation({
      query: (id) => {
        return {
          url: `/professions/${id}`,
          method: 'DELETE',
        }
      },
      onQueryStarted: onMutationStarted,
      invalidatesTags: ['Professions'],
    }),
    // Questions Api's
    getQuestions: builder.query({
      query: (params) => {
        return {
          url: `/questions/all`,
          method: 'GET',
          params: { ...params },
        }
      },
      providesTags: ['Questions'],
      keepUnusedDataFor: 0,
    }),
    getCategoryAndSkills: builder.query({
      query: (params) => {
        return {
          url: `/questions/all-categories-skills`,
          method: 'GET',
          params: { ...params },
        }
      },
      keepUnusedDataFor: 0,
    }),
    uploadQuestions: builder.mutation({
      query: (data) => {
        return {
          url: `/questions/upload`,
          method: 'POST',
          body: data,
        }
      },
      onQueryStarted: onMutationStarted,
    }),
    updateQuestion: builder.mutation({
      query: (data) => {
        return {
          url: `/questions/${data.id}`,
          method: 'PUT',
          body: { ...data },
        }
      },
      onQueryStarted: onMutationStarted,
      invalidatesTags: ['Questions'],
    }),
    deleteQuestion: builder.mutation({
      query: (id) => {
        return {
          url: `/questions/${id}`,
          method: 'DELETE',
        }
      },
      onQueryStarted: onMutationStarted,
      invalidatesTags: ['Questions'],
    }),
    getAnswer: builder.query({
      query: (params) => {
        return {
          url: `/questions/answer`,
          method: 'GET',
          params: { ...params },
        }
      },
      onQueryStarted: onMutationStarted,
    }),
    answerQuestions: builder.mutation({
      query: (data) => {
        return {
          url: `/questions/answer`,
          method: 'POST',
          body: { ...data },
        }
      },
      onQueryStarted: onMutationStarted,
    }),
  }),
})

// Export hooks for usage in functional components, which are
// auto-generated based on the defined endpoints
export const {
  useGetProfessionQuery,
  useAddProfessionMutation,
  useDeleteProfessionMutation,
  useGetQuestionsQuery,
  useGetCategoryAndSkillsQuery,
  useUploadQuestionsMutation,
  useUpdateQuestionMutation,
  useDeleteQuestionMutation,
  useGetAnswerQuery,
  useAnswerQuestionsMutation,
  useGetTokenMutation,
} = adminApi
