import { styled, Box } from '@mui/material'

const Item = styled(Box)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  color: theme.palette.white.main,
  height: '100%',
  display: 'flex',
  border: `2px solid ${theme.palette.white.light}`,
  borderRadius: '16px',
  '.MuiTypography-body2': {
    fontSize: '13px',
  },
  [theme.breakpoints.down('md')]: {
    h6: {
      fontSize: '1rem',
    },
    '.MuiTypography-body2': {
      fontSize: '11px',
    },
  },
}))

const Wrapper = styled(Box)(({ theme }) => ({
  '.icon-wrapper': {
    svg: {
      cursor: 'pointer',
      fontSize: '34px',
      padding: '5px',
      border: `2px solid ${theme.palette.white.light}`,
      borderRadius: '30px',
    },
  },
  [theme.breakpoints.down('md')]: {
    h3: {
      fontSize: '2rem',
    },
    body1: {
      fontSize: '0.9rem',
    },
  },
}))

export default { Wrapper, Item }
