import React, { useEffect, useRef, useState } from 'react'
import {
  Box,
  Button,
  CircularProgress,
  FormControlLabel,
  Radio,
  RadioGroup,
  Skeleton,
  Typography,
  useMediaQuery,
  useTheme,
} from '@mui/material'
import { useLocation, useNavigate, useSearchParams } from 'react-router-dom'
import EditIcon from '@mui/icons-material/Edit'
import DeleteForeverIcon from '@mui/icons-material/DeleteForever'
import MuiDrawer from '@Components/MuiDrawer/MuiDrawer'
import {
  useGetQuestionsQuery,
  useAnswerQuestionsMutation,
} from '@Services/admin'
import { useDispatch } from 'react-redux'
import { useForm } from 'react-hook-form'
import { errorAlert } from '../../Redux/Reducers/genericSlice'
import DeleteForm from './DeleteForm/DeleteForm'
import EditForm from './EditForm/EditForm'
import Styled from './style'

const Questions = () => {
  // Check if its the admin user
  const token = sessionStorage.getItem('token')
  const [searchParams] = useSearchParams()
  const professionId = searchParams.get('id')

  const navigate = useNavigate()
  const location = useLocation()
  const dispatch = useDispatch()
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.down('md'))
  const drawerRef = useRef(null)
  const [drawerType, setDrawerType] = useState('')
  const [selectedQuestion, setSelectedQuestion] = useState(null)
  const [answers, setAnswers] = useState({})
  const [answerQuestions, { isLoading: isSubmitting }] =
    useAnswerQuestionsMutation()
  const { data, isLoading, isError } = useGetQuestionsQuery(
    { professionId },
    {
      skip: !professionId,
    },
  )
  const { register, handleSubmit, reset } = useForm()

  useEffect(() => {
    if (!professionId) {
      navigate('/')
    }
  }, [location])

  useEffect(() => {
    reset()
    if (isError || (!isLoading && !data?.questions?.length)) {
      navigate('/')
    }

    if (!isLoading && data?.questions && !data?.questions?.length) {
      navigate('/')
      dispatch(errorAlert({ message: 'Sorry!, There are no questions.' }))
    }
  }, [isLoading])

  const renderDrawer = (key) => {
    switch (key) {
      case 'edit':
        return {
          title: 'Edit Question',
          component: (
            <EditForm
              closeDrawer={drawerRef?.current?.closeDrawer}
              data={selectedQuestion}
            />
          ),
        }
      case 'delete':
        return {
          title: 'Delete Question',
          component: (
            <DeleteForm
              data={selectedQuestion}
              closeDrawer={drawerRef?.current?.closeDrawer}
            />
          ),
        }

      default:
        return {}
    }
  }

  const skeletonView = (indx) => {
    return (
      <Styled.QuestionWrapper key={indx}>
        <Box display="flex" justifyContent="space-between">
          <Typography variant="h6" mb={2} fontWeight={600}>
            <Skeleton
              animation="wave"
              variant="rectangular"
              width={matches ? 220 : 270}
              height={30}
            />
          </Typography>
        </Box>
        <Typography variant="body2" mb={1}>
          <Skeleton
            animation="wave"
            variant="rectangular"
            width={matches ? 300 : 500}
            height={30}
          />
        </Typography>
        <Box>
          <RadioGroup defaultValue="">
            {Array.from({ length: 5 }).map(() => (
              <FormControlLabel
                value="xyz"
                control={<Radio />}
                label={
                  <Skeleton
                    variant="rectangular"
                    width={matches ? 100 : 130}
                    height={30}
                  />
                }
              />
            ))}
          </RadioGroup>
        </Box>
      </Styled.QuestionWrapper>
    )
  }

  const onSubmitButton = async (values) => {
    if (Object.keys(values).every((e) => values[e])) {
      const response = await answerQuestions({
        professionId,
        answers: values,
      })
      if (!response?.error) {
        navigate({
          pathname: '/result',
          search: `id=${response?.data?.answerId}`,
        })
      }
    } else {
      dispatch(errorAlert({ message: 'Please answer all the questions.' }))
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmitButton)}>
      <Styled.Wrapper
        maxWidth={550}
        sx={{ marginLeft: 'auto', marginRight: 'auto' }}
      >
        <Box
          sx={{
            position: 'sticky',
            top: 0,
            backgroundColor: 'black.main100',
            zIndex: 2,
          }}
        >
          <Box
            className="header"
            display="flex"
            alignItems="center"
            justifyContent="space-between"
          >
            <Box>
              <Typography variant="h5" mb={1} fontWeight={600}>
                {data?.profession?.professionName} Self Assesment
              </Typography>
              <Typography variant="body1">
                Chatper 1: Your experience is {data?.profession?.professionName}
              </Typography>
            </Box>
            <Button
              disabled={!(data?.questions?.length && !isSubmitting)}
              variant="contained"
              color="secondary"
              type="submit"
            >
              {isSubmitting ? (
                <CircularProgress color="inherit" size={20} />
              ) : (
                'Next Step'
              )}
            </Button>
          </Box>
          <Box sx={{ mt: 1 }}>
            <Styled.BorderLinearProgress
              variant="determinate"
              value={
                data?.questions?.length
                  ? (Object.keys(answers).filter((e) => answers[e])?.length /
                      data?.questions?.length) *
                    100
                  : 0
              }
            />
          </Box>
        </Box>
        <Box mt={4}>
          {isLoading
            ? Array.from({ length: 4 }).map((_, indx) => skeletonView(indx))
            : data?.questions?.map((question) => (
                <Styled.QuestionWrapper>
                  <Box display="flex" justifyContent="space-between">
                    <Typography variant="h6" mb={2} fontWeight={600}>
                      {question.question}
                    </Typography>
                    {token && (
                      <Box
                        className="icons-wrapper"
                        width={120}
                        textAlign="right"
                      >
                        <EditIcon
                          onClick={async () => {
                            await setSelectedQuestion({
                              ...question,
                              ...{
                                professionId,
                              },
                            })
                            await setDrawerType('edit')
                            drawerRef?.current?.openDrawer()
                          }}
                        />
                        <DeleteForeverIcon
                          onClick={async () => {
                            await setSelectedQuestion(question)
                            await setDrawerType('delete')
                            drawerRef?.current?.openDrawer()
                          }}
                          sx={{ ml: 0.5 }}
                        />
                      </Box>
                    )}
                  </Box>
                  {/* <Typography variant="body2" mb={1}>
                    {question.description}
                  </Typography> */}
                  <Box>
                    <RadioGroup
                      defaultValue=""
                      onChange={(e) => {
                        setAnswers({
                          ...answers,
                          ...{ [question._id]: e.target.value },
                        })
                      }}
                    >
                      {question.answerOptions.map((answer, index) => (
                        <FormControlLabel
                          value={index + 1}
                          control={<Radio />}
                          label={answer}
                          {...register(question._id)}
                        />
                      ))}
                    </RadioGroup>
                  </Box>
                </Styled.QuestionWrapper>
              ))}
        </Box>
        <MuiDrawer
          title={renderDrawer(drawerType)?.title}
          ref={drawerRef}
          element={null}
        >
          {drawerRef && renderDrawer(drawerType)?.component}
        </MuiDrawer>
      </Styled.Wrapper>
    </form>
  )
}

export default Questions
