import React, { useEffect, useRef, useState } from 'react'
import {
  Box,
  Button,
  FormControlLabel,
  Grid,
  Radio,
  RadioGroup,
  Skeleton,
  Typography,
  useMediaQuery,
  useTheme,
} from '@mui/material'
import { useLocation, useNavigate } from 'react-router-dom'
import CloudUploadIcon from '@mui/icons-material/CloudUpload'
import CategoryIcon from '@mui/icons-material/Category'
import DeleteForeverIcon from '@mui/icons-material/DeleteForever'
import MuiDrawer from '@Components/MuiDrawer/MuiDrawer'
import { useGetProfessionQuery } from '@Services/admin'
import { useDispatch } from 'react-redux'
import { errorAlert } from '../../Redux/Reducers/genericSlice'
import DeleteForm from './DeleteForm/DeleteForm'
import UploadForm from './UploadForm/UploadForm'
import AddForm from './AddForm/AddForm'
import AdminForm from './AdminForm/AdminForm'
import Styled from './style'

const Profession = () => {
  // Check if its the admin user
  const token = sessionStorage.getItem('token')

  const navigate = useNavigate()
  const dispatch = useDispatch()
  const location = useLocation()
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.down('md'))
  const drawerRef = useRef(null)
  const [drawerType, setDrawerType] = useState('')
  const [selectedProfession, setProfession] = useState('')
  const { data, isLoading } = useGetProfessionQuery('', {
    skip: location.pathname.includes('admin'),
  })

  useEffect(() => {
    if (location.pathname.includes('admin')) {
      setDrawerType('login')
      drawerRef?.current?.openDrawer()
    }
  }, [location])

  const renderDrawer = (key) => {
    switch (key) {
      case 'login':
        return {
          title: 'Admin Access',
          component: (
            <AdminForm closeDrawer={drawerRef?.current?.closeDrawer} />
          ),
        }
      case 'add':
        return {
          title: 'Add Profession',
          component: <AddForm closeDrawer={drawerRef?.current?.closeDrawer} />,
        }
      case 'upload':
        return {
          title: 'Upload Questions',
          component: (
            <UploadForm closeDrawer={drawerRef?.current?.closeDrawer} />
          ),
        }
      case 'delete':
        return {
          title: 'Delete Profession',
          component: (
            <DeleteForm closeDrawer={drawerRef?.current?.closeDrawer} />
          ),
        }

      default:
        return {}
    }
  }

  const skeletonView = (e) => {
    return (
      <Grid item xs={6} key={e}>
        <Styled.Item>
          <FormControlLabel
            value="xyz"
            control={<Radio />}
            label={
              <Box>
                <Skeleton
                  animation="wave"
                  variant="rectangular"
                  width={matches ? 50 : 80}
                  height={20}
                  sx={{ mb: 1 }}
                />
                <Skeleton
                  variant="rectangular"
                  width={matches ? 110 : 220}
                  height={30}
                />
              </Box>
            }
          />
        </Styled.Item>
      </Grid>
    )
  }

  return (
    <Styled.Wrapper>
      <Box maxWidth={600} sx={{ marginLeft: 'auto', marginRight: 'auto' }}>
        <Typography variant="h3" align="center" mb={1}>
          Please mind the gap!
        </Typography>
        <Typography variant="body1" align="center">
          Check in 5 minutes
        </Typography>
        <Typography variant="body1" align="center">
          how to go from good to great
        </Typography>
        <Box position="relative" mt={6}>
          <Typography variant="body2" align="center">
            What is your discipline?
          </Typography>
          {token && (
            <Box
              className="icon-wrapper"
              display="flex"
              justifyContent="center"
              mt={2}
            >
              <CategoryIcon
                onClick={async () => {
                  await setDrawerType('add')
                  drawerRef?.current?.openDrawer()
                }}
              />
              <CloudUploadIcon
                sx={{ ml: 0.5 }}
                onClick={async () => {
                  await setDrawerType('upload')
                  drawerRef?.current?.openDrawer()
                }}
              />
              <DeleteForeverIcon
                className="delete-icon"
                sx={{ ml: 0.5 }}
                onClick={async () => {
                  await setDrawerType('delete')
                  drawerRef?.current?.openDrawer()
                }}
              />
            </Box>
          )}
        </Box>
        <Box mt={2}>
          <RadioGroup
            aria-labelledby="categories-label"
            defaultValue=""
            name="categories-group"
          >
            <Grid container spacing={2}>
              {isLoading
                ? Array.from({ length: 4 }).map((_, indx) => skeletonView(indx))
                : (data?.professions || []).map((profession) => (
                    <Grid item xs={6} key={profession._id}>
                      <Styled.Item>
                        <FormControlLabel
                          value={profession.professionName}
                          control={<Radio />}
                          onChange={(e) => {
                            if (e.target.checked)
                              setProfession({
                                id: profession._id,
                                name: profession.professionName,
                              })
                          }}
                          label={
                            <Box>
                              <Typography variant="h6" fontWeight={600}>
                                {profession.professionName}
                              </Typography>
                              <Typography variant="body2">
                                {profession.description}
                              </Typography>
                            </Box>
                          }
                        />
                      </Styled.Item>
                    </Grid>
                  ))}
            </Grid>
          </RadioGroup>
        </Box>
        <Button
          disabled={!selectedProfession}
          fullWidth
          variant="contained"
          color="secondary"
          sx={{ mt: 2 }}
          onClick={() => {
            if (!selectedProfession)
              dispatch(errorAlert({ message: 'Select the profession.' }))
            else
              navigate({
                pathname: '/questions',
                search: `id=${selectedProfession?.id}`,
              })
          }}
        >
          Start Assesment
        </Button>
      </Box>
      <MuiDrawer
        isClosable={drawerType !== 'login'}
        title={renderDrawer(drawerType)?.title}
        ref={drawerRef}
        element={null}
      >
        {drawerRef && renderDrawer(drawerType)?.component}
      </MuiDrawer>
    </Styled.Wrapper>
  )
}

export default Profession
