import React, { useState } from 'react'
import PropTypes from 'prop-types'
import {
  Box,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  OutlinedInput,
  Select,
} from '@mui/material'
import {
  useUpdateQuestionMutation,
  useGetCategoryAndSkillsQuery,
} from '@Services/admin'
import { useDispatch } from 'react-redux'
import { errorAlert } from '../../../Redux/Reducers/genericSlice'

const EditForm = ({ data, closeDrawer }) => {
  const dispatch = useDispatch()
  const [question, setQuestion] = useState(data?.question)
  const [description, setDescription] = useState(data?.description)
  const [skills, setSkills] = useState(data?.skills || [])
  const [skillName, setSkillName] = useState('')
  const [categories, setCategories] = useState(data?.categories || [])
  const [categoryName, setCategoryName] = useState('')
  const [updateQuestion, { isLoading }] = useUpdateQuestionMutation()
  const { data: categoriesAndSkills, isLoading: apiLoading } =
    useGetCategoryAndSkillsQuery(
      {
        professionId: data.professionId,
      },
      { skip: !data.professionId },
    )

  const validateForm = () => {
    if (!question?.trim()) return false
    if (!description?.trim()) return false
    if (
      !categories?.length ||
      (categories.includes('other') && !categoryName?.trim())
    )
      return false
    if (!skills?.length || (skills.includes('other') && !skillName?.trim()))
      return false

    return true
  }

  return (
    <Box sx={{ overflow: 'auto', height: 'calc(100vh - 110px)' }}>
      <Box>
        <InputLabel sx={{ mb: 1 }}>Question</InputLabel>
        <FormControl fullWidth sx={{ mb: 2 }}>
          <OutlinedInput
            hiddenLabel
            placeholder="Please enter question."
            value={question}
            onChange={(event) => {
              setQuestion(event.target.value)
            }}
          />
        </FormControl>
        <InputLabel sx={{ mb: 1 }}>Description</InputLabel>
        <FormControl fullWidth sx={{ mb: 2 }}>
          <OutlinedInput
            hiddenLabel
            placeholder="Please enter description."
            value={description}
            onChange={(event) => {
              setDescription(event.target.value)
            }}
          />
        </FormControl>
        <InputLabel sx={{ mb: 1 }}>Categories</InputLabel>
        <FormControl fullWidth sx={{ mb: 2 }}>
          <Select
            disabled={apiLoading}
            multiple
            fullWidth
            displayEmpty
            value={categories}
            onChange={(event) => {
              if (
                event.target.value.includes('other') &&
                !categories.includes('other')
              ) {
                setCategories(['other'])
              } else {
                setCategories(event.target.value.filter((e) => e !== 'other'))
              }
            }}
            input={<OutlinedInput />}
            inputProps={{ 'aria-label': 'Without label' }}
          >
            <MenuItem disabled value="">
              <em>Select Categories</em>
            </MenuItem>
            {categoriesAndSkills?.allCategories.map((category) => (
              <MenuItem key={category} value={category}>
                {category}
              </MenuItem>
            ))}
            <MenuItem value="other">Other</MenuItem>
          </Select>
        </FormControl>
        {categories.includes('other') && (
          <>
            <InputLabel sx={{ mb: 1 }}>Category Name</InputLabel>
            <FormControl fullWidth sx={{ mb: 2 }}>
              <OutlinedInput
                hiddenLabel
                placeholder="Please enter category"
                value={categoryName}
                onChange={(event) => {
                  setCategoryName(event.target.value)
                }}
              />
            </FormControl>
          </>
        )}
        <InputLabel sx={{ mb: 1 }}>Skills</InputLabel>
        <FormControl fullWidth sx={{ mb: 2 }}>
          <Select
            disabled={apiLoading}
            multiple
            fullWidth
            displayEmpty
            value={skills}
            onChange={(event) => {
              if (
                event.target.value.includes('other') &&
                !skills.includes('other')
              ) {
                setSkills(['other'])
              } else {
                setSkills(event.target.value.filter((e) => e !== 'other'))
              }
            }}
            input={<OutlinedInput />}
            inputProps={{ 'aria-label': 'Without label' }}
          >
            <MenuItem disabled value="">
              <em>Select Skills</em>
            </MenuItem>
            {categoriesAndSkills?.allSkills.map((category) => (
              <MenuItem key={category} value={category}>
                {category}
              </MenuItem>
            ))}
            <MenuItem value="other">Other</MenuItem>
          </Select>
        </FormControl>
        {skills.includes('other') && (
          <>
            <InputLabel sx={{ mb: 1 }}>Skill Name</InputLabel>
            <FormControl fullWidth sx={{ mb: 2 }}>
              <OutlinedInput
                hiddenLabel
                placeholder="Please enter skill"
                value={skillName}
                onChange={(event) => {
                  setSkillName(event.target.value)
                }}
              />
            </FormControl>
          </>
        )}
      </Box>
      <Box width="100%" position="absolute" bottom={0} sx={{ mt: 2 }}>
        <Button
          disabled={isLoading}
          fullWidth
          variant="contained"
          color="secondary"
          onClick={async () => {
            if (validateForm()) {
              const response = await updateQuestion({
                id: data._id,
                question,
                description,
                categories: categories.includes('other')
                  ? [categoryName]
                  : categories,
                skills: skills.includes('other') ? [skillName] : skills,
              })
              if (!response.error) {
                closeDrawer()
              }
            } else {
              dispatch(errorAlert({ message: 'Please fill all the fields.' }))
            }
          }}
        >
          Submit
        </Button>
      </Box>
    </Box>
  )
}

export default EditForm

EditForm.defaultProps = {
  closeDrawer: () => null,
  data: null,
}

EditForm.propTypes = {
  closeDrawer: PropTypes.func,
  data: PropTypes.oneOfType([PropTypes.object]),
}
